<template>
  <div class="navBox">
    <div class="logo">
      <img src="data:image/png;base64," alt="" style="
      display: inline-block;" />
      <span class="logo_title">店铺装修</span>
    </div>
    <el-button class="returnPathBtn" type="info" v-if="wholsePath" @click="toWholsePath">返回商城</el-button>
  </div>
</template>
<script>
export default {
  computed: {
    wholsePath () {
      return localStorage.getItem('wholsePath')
    }
  },
  methods: {
    toWholsePath () {
      window.location.href = this.wholsePath
    }
  }
}
</script>
<style lang="less" scoped>
  .navBox {
    height:50px;
    background:white;
    .logo {
      padding-left: 12px;
      display: inline-block;
      padding-top:10px;
      .logo_title {
        font-size: 12px;
        color: #2e2f30;
      }
    }
    .returnPathBtn {
      margin:6px 20px 0 0;
      float:right;
    }
  }
</style>



